:root {
  --brand-purple: #5A2D82;
  --brand-yellow: #FDB813;
  --soft-purple: rgba(90, 45, 130, 0.08);
  --background: #ffffff;
  --foreground: #0b0b0d;
  --black: #0b0b0d;
  --white: #ffffff;
  --muted: rgba(11, 11, 13, 0.04);
  --border: rgba(11, 11, 13, 0.10);
  --shadow-sm: 0 1px 2px rgba(0, 0, 0, 0.06);
  --shadow-md: 0 10px 30px rgba(0, 0, 0, 0.10);
}
html,
body {
  height: 100%;
  overflow-x: hidden;
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: var(--font-sans), system-ui, -apple-system, Segoe UI, Roboto,
    Arial, Helvetica, sans-serif;
  line-height: 1.5;
  letter-spacing: -0.01em;
}
:where(h1, h2, h3, h4) {
  letter-spacing: -0.03em;
  color: var(--brand-purple);
}
a {
  color: inherit;
  text-decoration: none;
}
a:not(.btn):hover {
  color: var(--brand-purple);
}
::selection {
  background: rgba(90, 45, 130, 0.18);
}
:focus-visible {
  outline: 2px solid var(--brand-purple);
  outline-offset: 3px;
}
input,
textarea,
select {
  background: var(--white);
  color: var(--black);
}
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 0.9rem;
  padding: 0.85rem 1.25rem;
  font-weight: 700;
  transition: opacity 200ms ease, transform 200ms ease, background 200ms ease,
    color 200ms ease;
}
.btn:active {
  transform: translateY(1px);
}
.btn-purple {
  background: var(--brand-purple);
  color: var(--white);
}
.btn-purple:hover {
  background: var(--brand-purple);
  color: var(--white);
  opacity: 0.95;
}
.btn-black {
  background: var(--black);
  color: var(--white);
}
.btn-black:hover {
  opacity: 0.95;
}
.btn-yellow {
  background: var(--brand-yellow);
  color: var(--black);
}
.btn-yellow:hover {
  opacity: 0.95;
}
@keyframes marquee {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}
.animate-marquee {
  animation: marquee 18s linear infinite;
}
@media (prefers-reduced-motion: reduce) {
  .animate-marquee {
    animation: none;
  }
}
